const changeSetting = (state, payload) => {
  state.setting = { ...state.setting, ...payload };
};

const changeLoading = (state, payload) => {
  state.loading = payload;
};

const changePingData = (state, payload) => {
  state.pingData = payload;
};

const changeIframes = (state, payload) => {
  state.iframes = payload;
};

const changeAppDetails = (state, payload) => {
  state.appDetails = payload;
};

const changeAppDetailsStatus = (state, payload) => {
  const appName = payload.service;
  const isEnabled = payload.isEnabled;
  const appDetailIndex = state.appDetails.findIndex(app => app.service == appName);
  if (appDetailIndex == undefined) {
    state.appDetails[appDetailIndex].is_enabled = isEnabled;
  }
};

const changeMqttConfig = (state, payload) => {
  state.mqttConfig = { ...state.mqttConfig, ...payload };
};

const changeInstalledServices = (state, payload) => {
  state.installedServices = payload;
};

const changeMbNetworksData = (state, payload) => {
  state.mbNetworks = payload;
};

const changeServicesConfigured = (state, payload) => {
  state.isServicesConfigured = { ...state.isServicesConfigured, ...payload };
};

const changeRdNetworksData = (state, payload) => {
  state.rdNetworks = payload;
};

const changeMqttRestBridgeConfig = (state, payload = {}) => {
  state.mqttRestBridgeConfig = { ...payload };
  state.isMaster = (payload && payload.master) || false;
};

const changeBulkAppDownloadState = (state, payload) => {
  state.currentBulkAppDownloadState = payload;
};

const changeBmNetworksData = (state, payload) => {
  state.bmNetworks = payload;
};

const changeSwitchedSlaveState = (state, payload) => {
  state.isSwitchedSlave = payload;
};

const changeAppRerenderCount = (state) => {
  state.rerenderCount = state.rerenderCount++;
};

export default {
  changeSetting,
  changeSwitchedSlaveState,
  changeAppRerenderCount,
  changePingData,
  changeLoading,
  changeIframes,
  changeAppDetails,
  changeMqttConfig,
  changeMbNetworksData,
  changeRdNetworksData,
  changeAppDetailsStatus,
  changeInstalledServices,
  changeServicesConfigured,
  changeBulkAppDownloadState,
  changeMqttRestBridgeConfig,
  changeBmNetworksData,
};
